ht: 0;\n      bottom: 0;\n      left: 0;\n      background: transparent;\n      content: '';\n    }\n  }\n}\n.button-group-base(@btnClassName) {\n  position: relative;\n  display: inline-block;\n  > .@{btnClassName},\n  > span > .@{btnClassName} {\n    position: relative;\n    &:hover,\n    &:focus,\n    &:active,\n    &.active {\n      z-index: 2;\n    }\n    &:disabled {\n      z-index: 0;\n    }\n  }\n  > .@{btnClassName}-icon-only {\n    font-size: @font-size-base;\n  }\n  // size\n  &-lg > .@{btnClassName},\n  &-lg > span > .@{btnClassName} {\n    .button-size(@btn-height-lg; @btn-padding-horizontal-lg; @btn-font-size-lg; 0);\n  }\n  &-lg > .@{btnClassName}.@{btnClassName}-icon-only {\n    .square(@btn-height-lg);\n    padding-right: 0;\n    padding-left: 0;\n  }\n  &-sm > .@{btnClassName},\n  &-sm > span > .@{btnClassName} {\n    .button-size(@btn-height-sm; @btn-padding-horizontal-sm; @font-size-base; 0);\n    > .@{iconfont-css-prefix} {\n      font-size: @font-size-base;\n    }\n  }\n  &-sm > .@{btnClassName}.@{btnClassName}-icon-only {\n    .square(@btn-height-sm);\n    padding-right: 0;\n    padding-left: 0;\n  }\n}\n// Base styles of buttons\n// --------------------------------------------------\n.btn() {\n  position: relative;\n  display: inline-block;\n  font-weight: @btn-font-weight;\n  white-space: nowrap;\n  text-align: center;\n  background-image: none;\n  border: @btn-border-width @btn-border-style transparent;\n  box-shadow: @btn-shadow;\n  cursor: pointer;\n  transition: all 0.3s @ease-in-out;\n  user-select: none;\n  touch-action: manipulation;\n  .button-size(\n    @btn-height-base; @btn-padding-horizontal-base; @font-size-base; @btn-border-radius-base\n  );\n  > .@{iconfont-css-prefix} {\n    line-height: 1;\n  }\n  &,\n  &:active,\n  &:focus {\n    outline: 0;\n  }\n  &:not([disabled]):hover {\n    text-decoration: none;\n  }\n  &:not([disabled]):active {\n    outline: 0;\n    box-shadow: none;\n  }\n  &.disabled,\n  &[disabled] {\n    cursor